.formLabel {
  display: block;
  margin-bottom: 5px;
  color: #1677ff;
  font-weight: 500;
}

.input,
.textArea {
  width: 100%;
  padding: 5px 10px;
  border: 1px solid #afafaf;
  border-radius: 5px;
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  font-size: 14px;
  letter-spacing: 0.7px;
}
.input:focus,
.textArea:focus {
  outline: none;
}

.input {
  display: block;
  height: 30px;
  margin-bottom: 7px;
}

.textArea {
  height: 70px;
  resize: none;
}